import React from 'react';
import loadingGif from './loading.gif';
import Error from './Error';
import Result from './Result';

const people = ['해롤드', '영재', '벤', '재엽', '예슬', '정현', '동은', '노엘', '혜리', '주원', '세연', '예원'];

const peopleObj = {
    '해롤드': 1,
    '영재' : 2,
    '벤' : 3,
    '재엽' : 4, 
    '예슬' : 5,
    '정현' : 6,
    '동은' : 7,
    '노엘' : 8,
    '혜리' : 9,
    '주원' : 10,
    '세연' : 11,
    '예원' : 12
}

const pick = {
    3: 7,
    8: 5,
    1: 8,
    9: 4,
    7: 12,
    12: 6,
    10: 1,
    2: 9,
    5: 3,
    4: 10,
    11: 2,
    6: 11,
}

// const pick = {
//     3: 4,
//     8: 9,
//     1: 2,
//     9: 10,
//     7: 8,
//     12: 1,
//     10: 11,
//     2: 3,
//     5: 6,
