// }

const reverseObj = (pick) => {
    let reversed = {};
    for (let key in pick) {
        reversed[pick[key]] = key;
    }
    return reversed;
}

let reversedPick = reverseObj(pick);


const encrypt = code => {
    const alpha = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
    const shift = 'PBMFVITDESZNLCJRAYQKGUWOHX9876543210';

    let result = ''; 
    for (let char of code) {
        let index = alpha.indexOf(char);
        result += shift[index];
    }
    return result;
}

let encryptCodes = codes => {
    let result = {};
    for (let key in codes) {
        let encrypted = encrypt(codes[key]);
        result[key] = encrypted;
    }
    return result;
}

const codes = {
    '해롤드': 'RXZRS',
    '영재': 'JLI10',
    '벤': 'MWHY5',
    '재엽': 'G5PI3',
    '예슬': 'RW47O',
    '정현': 'MF392',
    '동은': 'VC0IM',
    '노엘': '6DIUK',
    '혜리': 'KQUYM',
    '주원': 'K3BU4',
    '세연': 'XBIXY',
    '예원': 'HIGWV'
}

class Manito extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ownName: '',
            resultAvailable: false,
            manitoName: '',
            submitDisabled: true,
            checkboxDisabled: true,
            codeDisabled: true,
            loading: false,
            secretCode: '',
            error: false,
            message: '',
            showResult: false,
            password: ''
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleCheckbox = this.handleCheckbox.bind(this);
        this.handleCode = this.handleCode.bind(this);
        this.showResult = this.showResult.bind(this);
        this.handlePassword = this.handlePassword.bind(this);
    }

    handleCode(e) {
        let input = e.target.value;
        this.setState({
            secretCode: input,
        })
    }

    handleChange(e) {
        if (e.target.value !== "default") {
            let ownName = e.target.value;
            let ownNum = (peopleObj[ownName]);
            let manitoNum = pick[ownNum];

            this.setState({
                ownName: ownName,
                manitoName: people[manitoNum - 1],
                checkboxDisabled: false,
                codeDisabled: false
            })
        }
    }

    handleClick() {
        if (encrypt(this.state.secretCode.toUpperCase()) === codes[this.state.ownName]) {
            this.setState({
                loading: true
            })
    
            this.reveal = setTimeout(() => this.setState({
                loading: false,
                resultAvailable: true
            }), 8000);
        } else {
            this.setState({
                error: true,
                message: 'Wrong code. Try again.',
            })

            setTimeout(() => this.setState({
                secretCode: '',
                error: false,
                message: ''
            }), 2000)
        }
    }

    handleCheckbox(e) {
        this.setState({
            submitDisabled: !e.target.checked
        })
    }

    handlePassword(e) {
        let input = e.target.value;
        this.setState({
            password: input
        })
    }

    showResult() {
        if (encrypt((this.state.password).toUpperCase()) === 'LPCEKJ7979') {
            this.setState({
                showResult: true
            })
        }
    }

    render() {
        if (this.state.loading) {
            return <img alt="loading" src={loadingGif}></img>
        } else if (this.state.resultAvailable && !this.state.loading) {
            return <span>당신은 <span className="manito">{this.state.manitoName}님</span> 의 마니또 입니다.</span>
        } else if (this.state.showResult) {
            return <Result people={people} peopleObj={peopleObj} pick={reversedPick} />
        } else {
            return (
                <div>
                    <input className="input" placeholder="비밀번호" value={this.state.password} onChange={this.handlePassword} required></input><br/>
                    <button className="button1" onClick={this.showResult}>결과를 맞춰봐요</button>
                    <p>본인 이름을 선택해주세요</p>
                    <select className="select-css" onChange={this.handleChange} defaultValue="default">
                        <option value="default" disabled={true}>Select</option>
                        {people.map((person, key) => {
                            return <option value={person} key={key}>{person}</option>
                        })}
                    </select><br/>

                    <input className="input" onChange={this.handleCode} placeholder="비밀코드" disabled={this.state.codeDisabled} value={this.state.secretCode} required></input><br/>
                    {this.state.error ? <Error message={this.state.message} /> : <div className="error"> </div> }
    
                    <input className="checkbox" type="checkbox" disabled={this.state.checkboxDisabled} onChange={this.handleCheckbox} required></input> 제 이름이 확실합니다<br/><br/>
                    <button className="button" disabled={this.state.submitDisabled} onClick={this.handleClick}>Submit</button>
                    {/* {
                        this.state.resultAvailable && !this.state.loading ? <p>{this.state.manitoName}</p> : null
                    }
                    <br/>
                    {
                        this.state.loading ? <img src={loadingGif}></img> : null
                    } */}
                </div>
            )
        }
        
    }
}

export default Manito;
